<template>
  <ul class="autocomplete">
   <li class="match" v-for="match in matches" :id="'match-'+match.id" @click="selectmatch(match.id)">
   {{match.name[0]}} {{match.name[1]}} - {{match.phone}} 
   </li>
   </ul>
</template>

<script>

//https://alligator.io/vuejs/add-v-model-support/
//@todo make so up/down change highlight
export default {
  name: 'Autocomplete',
  props: {
    query:String,
  },
  methods:{
    selectmatch:function(selid){
      this.$emit('selected',this.donors.filter((donor) => donor['id']==selid)[0]);
    }
  },
  computed:{
    matches(){
      if (this.query == ''){
        return [];
      }
      else{
        var filtered = this.donors.filter((donor) => 
            donor['name'][0].toLowerCase().includes(this.query.toLowerCase()) || 
            donor['name'][1].toLowerCase().includes(this.query.toLowerCase()) ||
            donor['phone'].includes(this.query)

          );
        
        return filtered;
      }
    }
  },
  data(){
  return{
  donors:[
    {'id':0,'name':['ab','testerson'],'phone':'12345','email':'test@test.com','business':'test'},
    {'id':1,'name':['cd','testerson'],'phone':'1233','email':'test@tes.com'},
    {'id':2,'name':['ef','tost'],'phone':'321','email':'t@test.cm'},
    {'id':3,'name':['gh','tast'],'phone':'4','email':'bbst@test.com'},
  ],

  highlightedsel:null,
  }
  }

}
</script>

